.page__logo {
  margin: 2em auto;
  max-width: 200px;
  display: block;
}

.page__title {
  font-size: 58px;
}

.page__sub-title {
  font-size: 24px;
}

.section__title {
  margin-bottom: $half-spacing;
}


// Room
.room-desk {
  max-width: 600px;
  margin-left: -130px;
  margin-top: 3rem;
}

.list-title {
  text-transform: uppercase;
  font-size: 80%;
  font-weight: bold;
}

.skill-img {
  max-width: 360px;
  width: 100%;
}

// Position
.position-top-left {
  position: absolute;
  top: 4em;
  left: 2em;
  max-width: 180px;
}

.position-top-right {
  position: absolute;
  top: 2em;
  right: 2em;
  max-width: 240px;
}

.position-bottom-right {
  position: absolute;
  bottom: 4em;
  right: 4em;
  max-width: 220px;
}

.container {
  @include zebra(); 
}


.kss-header {
  padding-left: $keyline;
}

.text--center {
  text-align: center;
}